@use 'sass:math';

// === MIXINS ===

@mixin roboto {
  font-family: 'Roboto', sans-serif;
}

// === / MIXINS ===

// === FUNCTIONS ===

@function strip-unit($num) {
  @return math.div($num, ($num * 0 + 1));
}

@function rem($num) {
  @return math.div(strip-unit($num), 16) * 1rem;
}

// === / FUNCTIONS ===

// === VARIABLES ===

$base: rem(4px);

$fw-light: 300;
$fw-regular: 400;
$fw-medium: 500;
$fw-bold: 700;

$fs-xs: rem(12px);
$fs-s: rem(14px);
$fs-m: rem(16px);
$fs-l: rem(18px);
$fs-xl: rem(20px);
$fs-xxl: rem(22px);

$transition: 0.3s;
$transition-01: 0.1s;
$transition-02: 0.2s;

$color-black: rgba(0, 0, 0, 1);
$color-white: rgba(255, 255, 255, 1);
$color-white-02: rgba(255, 255, 255, 0.2);
$color-white-03: rgba(255, 255, 255, 0.3);
$color-white-04: rgba(255, 255, 255, 0.4);
$color-white-05: rgba(255, 255, 255, 0.5);
$color-white-06: rgba(255, 255, 255, 0.6);
$color-white-07: rgba(255, 255, 255, 0.7);
$color-white-08: rgba(255, 255, 255, 0.8);
$color-white-09: rgba(255, 255, 255, 0.9);
$color-dark: rgba(51, 51, 51, 1);
$color-dark-02: rgba(51, 51, 51, 0.2);
$color-dark-03: rgba(51, 51, 51, 0.3);
$color-dark-04: rgba(51, 51, 51, 0.4);
$color-dark-05: rgba(51, 51, 51, 0.5);
